import styled from "styled-components";

const ListImportExcel = styled.div`
  align-items: center;
  display: flex;
  font-size: 0.93rem;
  justify-content: space-between;
  padding: 0.75rem 2rem;
  width: 100%;

  &:hover {
    background-color: #f8f8f8;
  }

  .description {
    flex: 4;

    .title {
      display: flex;
      font-weight: bold;

      .info {
        color: #afacac;
        display: none;
        font-size: 0.85rem;
        font-weight: normal;
        padding: 0 0.5rem;
        position: relative;
        margin-left: 0.5rem;

        &:before {
          content: ".";
          left: 0;
          height: 100%;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
        }
      }
    }
  }

  .action {
    display: flex;
    flex: 3;
    justify-content: space-between;
  }
`;

export default ListImportExcel;
