const actions = {
  // import
  importExcelReq: (key, body) => ({
    type: "IMPORT_EXCEL_REQUEST",
    payload: { body: body, key: key }
  }),

  // list
  getListData: (key, page, search, filter) => ({
    type: "GET_LIST_DATA_REQUEST",
    payload: {
      key: key,
      page: page,
      search: search,
      filter: filter
    }
  }),

  // other
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearWarning: () => ({
    type: "CLEAR_WARNING"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
