import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import NumberFormat from "../../App/NumberFormat";

import notification from "../../../components/notification";
import {
  Icon
  // Input,
  // Select,
  // Row,
  // Col
  // Divider,
  // Empty,
  // Spin
  // Upload
} from "antd";
import Button from "../../../components/uielements/button";
import CardWrapper, { Box } from "../../App/app.style";
import IntlMessages from "../../../components/utility/intlMessages";
import LayoutWrapper from "../../../components/utility/layoutWrapper";
import ListImportExcel from "./importExcel.style";
import ModalUpload from "./modalUpload";
import ModalView from "./modalView";
import PageHeader from "../../../components/utility/pageHeader";
import Scrollbars from "../../../components/utility/customScrollBar";
// import TableWrapper from "../../Tables/antTables/antTable.style";
import actions from "../../../redux/importExcel/actions";
import accountActions from "../../../redux/account/account/actions";
import departmentActions from "../../../redux/companyMaster/department/actions";
import itemCategoryActions from "../../../redux/item/itemCategory/actions";
import issueReceiptActions from "../../../redux/inventory/issueReceipt/actions";

import getLanguage from "../../App/MyLanguange";

const { showAccount, clearAccount, clearErrorField } = accountActions;
const { showDepartment } = departmentActions;
const { showItemCategory } = itemCategoryActions;
const { createIssueReceipt } = issueReceiptActions;

// const Option = Select.Option;
// const Search = Input.Search;

class ImportExcel extends Component {
  state = {
    activeData: null, //obj - menyimpan data dari dataSource yang dipilih
    clearModalUpload: false, //parameter untuk clear data modal upload
    visibleModalUpload: false, //bool - parameter visible untuk modal upload
    visibleModalView: false //bool - parameter visible untuk modal view
  };

  // Get All data diawal
  componentDidMount() {}

  // Display notif
  componentDidUpdate() {
    // Display error notif
    if (this.props.importExcel.error) {
      notification("error", this.props.importExcel.error);
      this.props.clearError();
    }
    if (this.props.importExcel.success) {
      notification("success", this.props.importExcel.success);
      this.props.clearSuccess();
    }
    if (this.props.importExcel.warning) {
      notification("warning", this.props.importExcel.warning);
      this.props.clearWarning();
    }
  }

  getList = (key, page) => {
    const { getListData } = this.props;
    getListData(key, page, null, "");
  };

  render() {
    const {
      accountItemsOB,
      adjustmentTypes,
      auth,
      createIssueReceipt,
      loadingForeignItemOB,
      locations
    } = this.props;
    const { user } = auth;
    const {
      foreignData,
      listData,
      loading,
      loadingList
    } = this.props.importExcel;

    const dataSource = [
      {
        key: "location",
        title: getLanguage({ id: "Location" }),
        file: "location"
      },
      // {
      //   key: "currency",
      //   title: getLanguage({ id: "Currency" }),
      //   file: "currency"
      // },
      {
        key: "cashFlowType",
        title: getLanguage({ id: "sidebar.cashFlowType" }),
        file: "cash_flow_type"
      },
      {
        key: "coaAccType",
        title:
          getLanguage({ id: "Chart Of Account" }) +
          " & " +
          getLanguage({ id: "sidebar.accountType" }),
        file: "account"
      },
      {
        key: "glAccount",
        title: getLanguage({ id: "purchaseOrders.GlAccount" }),
        file: "gl_account"
      },
      {
        key: "tax",
        title: getLanguage({ id: "tax" }),
        file: "tax"
      },
      {
        key: "cashBank",
        title: getLanguage({ id: "Cash Bank" }),
        file: "cash_bank"
      },
      {
        key: "paymentTerm",
        title: getLanguage({ id: "sidebar.paymentTerm" }),
        file: "payment_term"
      },
      {
        key: "paymentCategory",
        title: getLanguage({ id: "sidebar.paymentCategory" }),
        file: "payment_category"
      },
      {
        key: "supplierType",
        title: getLanguage({ id: "Supplier Type" }),
        file: "supplier_type"
      },
      {
        key: "supplier",
        title: getLanguage({ id: "Supplier" }),
        file: "supplier"
      },
      {
        key: "customerType",
        title: getLanguage({ id: "Customer Type" }),
        file: "customer_type"
      },
      {
        key: "customer",
        title: getLanguage({ id: "Customer" }),
        file: "customer"
      },
      {
        key: "jobTitle",
        title: getLanguage({ id: "Job Title" }),
        file: "job_title"
      },
      {
        key: "employee",
        title: getLanguage({ id: "Employee" }),
        file: "employee"
      },
      {
        key: "department",
        title: getLanguage({ id: "Department" }),
        file: "department"
      },
      {
        key: "project",
        title: getLanguage({ id: "Project" }),
        file: "project"
      },
      {
        key: "unit",
        title: getLanguage({ id: "Unit" }),
        file: "unit"
      },
      {
        key: "itemCategory",
        title: getLanguage({ id: "Item Category" }),
        file: "item_category"
      },
      {
        key: "item",
        title: getLanguage({ id: "Item" }),
        file: "item"
      },
      {
        key: "itemOB",
        title: getLanguage({ id: "itemOB" }),
        file: "item_opening_balance"
      },
      {
        key: "supplierOB",
        title: getLanguage({ id: "supplierOB" }),
        file: "supplier_opening_balance"
      },
      {
        key: "customerOB",
        title: getLanguage({ id: "customerOB" }),
        file: "customer_opening_balance"
      }
    ];

    let detailData = null;
    let loadingDetail = false;
    let showDetail = null;

    if (this.state.activeData) {
      if (this.state.activeData.key === "coaAccType") {
        detailData = this.props.account;
        loadingDetail = this.props.loadingAccount;
        showDetail = this.props.showAccount;
      } else if (this.state.activeData.key === "department") {
        detailData = this.props.department;
        loadingDetail = this.props.loadingDepartment;
        showDetail = this.props.showDepartment;
      } else if (this.state.activeData.key === "itemCategory") {
        detailData = this.props.itemCategory;
        loadingDetail = this.props.loadingItemCategory;
        showDetail = this.props.showItemCategory;
      }
    }

    return (
      <LayoutWrapper>
        <ModalUpload
          accounts={accountItemsOB}
          activeData={this.state.activeData}
          adjustmentTypes={adjustmentTypes}
          handleCancel={() =>
            this.setState({
              visibleModalUpload: !this.state.visibleModalUpload
            })
          }
          importExcel={this.props.importExcel}
          importExcelReq={this.props.importExcelReq}
          loading={loading}
          loadingForeignItemOB={loadingForeignItemOB}
          locations={locations}
          visible={this.state.visibleModalUpload}
        ></ModalUpload>
        <ModalView
          detailData={detailData}
          activeData={this.state.activeData}
          clearAccount={this.props.clearAccount}
          clearErrorField={this.props.clearErrorField}
          foreignData={foreignData}
          getList={this.getList}
          handleCancel={() =>
            this.setState({ visibleModalView: !this.state.visibleModalView })
          }
          importExcel={this.props.importExcel}
          listData={listData}
          loading={loadingList}
          loadingDetail={loadingDetail}
          showDetail={showDetail}
          visible={this.state.visibleModalView}
        ></ModalView>
        <Box>
          <PageHeader
            title={<IntlMessages id="importExcel" />}
            style={{ margin: 0 }}
          ></PageHeader>

          <CardWrapper>
            <Scrollbars style={{ width: "100%" }}>
              {dataSource.map((data, index) => (
                <ListImportExcel key={index}>
                  <div className="description">
                    <div className="title">
                      <div>{data.title}</div>
                      <div className="info">info</div>
                    </div>
                  </div>
                  <div className="action">
                    <div style={{ display: "flex" }}>
                      {/*  button modal upload */}
                      <Button
                        type="primary"
                        onClick={() => {
                          // apabila data merupakan item opening balance maka panggil data pendukung
                          if (data.key === "itemOB") {
                            createIssueReceipt();
                          }

                          this.setState({
                            activeData: data,
                            visibleModalUpload: !this.state.visibleModalUpload
                          });
                        }}
                      >
                        <Icon type="upload" />{" "}
                        <IntlMessages id="upload"></IntlMessages>
                      </Button>
                    </div>
                    <div style={{ margin: "0 1rem" }}>
                      {/* button view list */}
                      <Button
                        type="default"
                        onClick={() => {
                          this.getList(data.key, 1);
                          this.setState({
                            activeData: data,
                            visibleModalView: !this.state.visibleModalView
                          });
                        }}
                        loading={loadingList}
                        style={{ marginLeft: "0%" }}
                      >
                        <IntlMessages id="button.view"></IntlMessages>
                      </Button>
                    </div>
                    <div>
                      {/* button download template */}
                      <a
                        // target="_blank"
                        // rel="noopener noreferrer"
                        href={
                          localStorage.getItem("server") +
                          `templates/excel_importer/v.${
                            auth && user ? user.db_version : ""
                          }/equal_${data.file}.xlsx`
                        }
                        download
                      >
                        <Button
                          type="default"
                          // className="invoiceRstBtn"
                          // onClick={this.handleUpload}
                          loading={loading}
                          style={{ marginLeft: "0%" }}
                        >
                          <Icon type="download" />{" "}
                          <IntlMessages id="downloadTemplate"></IntlMessages>
                        </Button>
                      </a>
                    </div>
                  </div>
                </ListImportExcel>
              ))}
            </Scrollbars>
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    account: state.account.account,
    accountItemsOB: state.issueReceipt.accounts,
    adjustmentTypes: state.issueReceipt.adjustmentTypes,
    auth: state.auth,
    department: state.department.department,
    importExcel: state.importExcel,
    itemCategory: state.itemCategory.item_category,
    loadingAccount: state.account.loadingAccount,
    loadingDepartment: state.department.loadingDepartment,
    loadingForeignItemOB: state.issueReceipt.loadingForeign,
    loadingItemCategory: state.itemCategory.loadingItemCategory,
    locations: state.issueReceipt.locations
  };
};

export default compose(
  connect(mapStateToProps, {
    ...actions,
    clearAccount,
    clearErrorField,
    itemCategoryActions,
    createIssueReceipt,
    showAccount,
    showDepartment,
    showItemCategory
  })
)(ImportExcel);
