import moment from "moment";

const CashBankColumns = [
  {
    title: getLanguage({ id: "code" }),
    dataIndex: "cash_bank_code",
    rowKey: "cash_bank_code",
    width: "15%",
    render: text => <span>{text}</span>
  },
  {
    title: getLanguage({ id: "name" }),
    dataIndex: "description",
    rowKey: "description",
    width: "20%",
    render: text => <span>{text}</span>
  },
  {
    title: getLanguage({ id: "cashBanks.add.bankAccountNumber" }),
    dataIndex: "bank_account_number",
    rowKey: "bank_account_number",
    width: "15%",
    render: text => {
      if (text) {
        return <span>{text}</span>;
      } else {
        return <span>{}</span>;
      }
    }
  },
  {
    title: getLanguage({ id: "sidebar.location" }),
    dataIndex: "location",
    rowKey: "location",
    width: "20%",
    render: (text, row) => {
      if (text) {
        return <span>{text.location_name}</span>;
      } else {
        return <span>{}</span>;
      }
    }
  },
  {
    title: getLanguage({ id: "items.tab.account" }),
    dataIndex: "account",
    rowKey: "account",
    width: "20%",
    render: (text, row) => {
      return <span>{text.account_code + " (" + text.account_name + ")"}</span>;
    }
  },

  {
    title: getLanguage({ id: "currencies.pageHeader" }),
    dataIndex: "account.currency",
    rowKey: "account.currency",
    width: "10%",
    align: "center",
    render: text => {
      return <span>{text.currency_code}</span>;
    }
  },
  {
    title: getLanguage({ id: "openingBalance" }),
    dataIndex: "opening_balance",
    rowKey: "opening_balance",
    width: "10%",
    align: "right",
    render: text => {
      return <NumberFormat value={parseFloat(text)} />;
    }
  },
  {
    title: getLanguage({ id: "status" }),
    dataIndex: "is_active",
    rowKey: "is_active",
    width: "10%",
    align: "center",
    render: text => {
      let className;
      let display;
      if (text === 1) {
        className = "active";
        display = getLanguage({ id: "active" });
      } else {
        className = "nonactive";
        display = getLanguage({ id: "inactive" });
      }
      return <StatusTag className={className}>{display}</StatusTag>;
    }
  }
];

