const ItemColumns = [
  {
    title: getLanguage({ id: "items.code" }),
    dataIndex: "item_code",
    rowKey: "item_code",
    width: "20%",
    render: text => <span>{text}</span>
  },
  {
    title: getLanguage({ id: "name" }),
    dataIndex: "item_name",
    rowKey: "item_name",
    width: "30%",
    render: text => <span>{text}</span>
  },
  {
    title: getLanguage({ id: "sidebar.itemCategory" }),
    dataIndex: "item_category.item_category_name",
    rowKey: "item_category.item_category_name",
    width: "30%",
    render: text => <span>{text}</span>
  },
  {
    title: getLanguage({ id: "items.itemType" }),
    dataIndex: "item_type",
    rowKey: "item_type",
    width: "10%",
    align: "center",
    render: text => {
      let className;
      let display;
      switch (text) {
        case 1:
          className = "primary";
          display = getLanguage({ id: "inventory" });
          break;
        case 2:
          className = "ordered";
          display = getLanguage({ id: "nonInventory" });
          break;
        case 3:
          className = "invoiced";
          display = getLanguage({ id: "grouping" });
          break;
        case 4:
          className = "closed";
          display = getLanguage({ id: "service" });
          break;
        case 5:
          className = "cancelled";
          display = getLanguage({ id: "roles.tab.asset" });
          break;
        default:
          className = "nonactive";
          display = getLanguage({ id: "undefined" });
          break;
      }

      return <StatusTag className={className}>{display}</StatusTag>;
    }
  },
  // {
  //   title: "Barcode",
  //   dataIndex: "barcode",
  //   rowKey: "barcode",
  //   width: "15%",
  //   render: text => <span>{text}</span>
  // },
  // {
  //   title: "SKU",
  //   dataIndex: "sku",
  //   rowKey: "sku",
  //   width: "15%",
  //   render: text => <span>{text}</span>
  // },
  // {
  //   title: "SKU Name",
  //   dataIndex: "sku_name",
  //   rowKey: "sku_name",
  //   width: "15%",
  //   render: text => <span>{text}</span>
  // },
  // {
  //   title: "Registered at",
  //   dataIndex: "registered_at.location_name",
  //   rowKey: "registered_at.location_name",
  //   width: "10%",
  //   render: text => <span>{text}</span>
  // },
  {
    title: getLanguage({ id: "status" }),
    dataIndex: "is_active",
    rowKey: "is_active",
    width: "10%",
    align: "center",
    render: text => {
      let className;
      let display;
      if (text === 1) {
        className = "active";
        display = getLanguage({ id: "active" });
      } else if (text === -1) {
        className = "Primary";
        display = getLanguage({ id: "new" });
      } else {
        className = "nonactive";
        display = getLanguage({ id: "inactive" });
      }
      return <StatusTag className={className}>{display}</StatusTag>;
    }
  }
];

