const ProjectColumns = [
  {
    title: getLanguage({ id: "code" }),
    dataIndex: "project_code",
    rowKey: "project_code",
    render: text => <span>{text}</span>,
    width: "11%"
  },
  {
    title: getLanguage({ id: "name" }),
    dataIndex: "project_name",
    rowKey: "project_name",
    render: text => <span>{text}</span>,
    width: "11%"
  },
  {
    title: getLanguage({ id: "projects.parent" }),
    dataIndex: "parent",
    rowKey: "parent",
    render: text => {
      if (text !== null) {
        return <span> {text.project_name} </span>;
      } else {
        return <span />;
      }
    },
    width: "11%"
  },
  {
    title: getLanguage({ id: "projects.manager" }),
    dataIndex: "employee_manager",
    rowKey: "employee_manager",
    render: text => {
      if (text !== null && text.last_name !== null) {
        return <span> {text.first_name + " " + text.last_name} </span>;
      } else if (text !== null && text.last_name === null) {
        return <span> {text.first_name} </span>;
      } else {
        return <span />;
      }
    },
    width: "11%"
  },
  {
    title: getLanguage({ id: "customers.pageHeader" }),
    dataIndex: "customer",
    rowKey: "customer",
    render: text => {
      if (text !== null) {
        return <span> {text.customer_name} </span>;
      } else {
        return <span />;
      }
    },
    width: "11%"
  },
  {
    title: getLanguage({ id: "startDate" }),
    dataIndex: "start_date",
    rowKey: "start_date",
    render: text => <span>{moment(text).format("DD/MM/YYYY")}</span>,
    width: "11%"
  },
  {
    title: getLanguage({ id: "endDate" }),
    dataIndex: "end_date",
    rowKey: "end_date",
    render: text => <span>{moment(text).format("DD/MM/YYYY")}</span>,
    width: "11%"
  },
  {
    title: getLanguage({ id: "progress" }),
    dataIndex: "progress",
    rowKey: "progress",
    render: text => (
      <Progress percent={text[0].project_history_progress * 100} size="small" />
    ),
    width: "11%"
  },
  {
    title: getLanguage({ id: "projects.status" }),
    dataIndex: "status",
    rowKey: "status",
    align: "center",
    render: text => {
      // let className;
      let display;
      if (text === 1) {
        display = getLanguage({ id: "new" });
      } else if (text === 2) {
        display = getLanguage({ id: "estimate" });
      } else if (text === 3) {
        display = getLanguage({ id: "approved" });
      } else if (text === 4) {
        display = getLanguage({ id: "started" });
      } else if (text === 5) {
        display = getLanguage({ id: "finished" });
      } else if (text === 6) {
        display = getLanguage({ id: "cancelled" });
      } else if (text === 7) {
        display = getLanguage({ id: "onHold" });
      }
      return <span>{display}</span>;
    },
    width: "11%"
  },
  {
    title: getLanguage({ id: "status" }),
    dataIndex: "is_active",
    rowKey: "is_active",
    align: "center",
    render: text => {
      let className;
      let display;
      if (text === 1) {
        className = "active";
        display = getLanguage({ id: "active" });
      } else {
        className = "nonactive";
        display = getLanguage({ id: "active" });
      }
      return <StatusTag className={className}>{display}</StatusTag>;
    },
    width: "11%"
  }
];

