import React, { Component } from "react";
import styled from "styled-components";
import moment from "moment";

import IntlMessages from "../../../components/utility/intlMessages";
import Button from "../../../components/uielements/button";
import {
  Alert,
  // Divider,
  Col,
  DatePicker,
  Form,
  Icon,
  // Input,
  Modal,
  Radio,
  Row,
  Select,
  // Spin,
  Upload
} from "antd";
import getLanguage from "../../App/MyLanguange";

import ModalPassword from "./modalPassword";

const WrapperModalUpload = styled.div`
  .ant-upload-list-item-name {
    white-space: pre-wrap;
    word-break: break-word;
  }

  .list-error {
    margin-bottom: 0.5rem;
    ul {
      margin-bottom: 0.4rem;
      li {
        margin: 0.25rem 0;
      }
    }
  }
`;

const { Option } = Select;
// const { TextArea } = Input;

class ModalUpload extends Component {
  state = {
    allowedExt: ["xlsx", "xls"], //menyimpan list ekstensi yang diterima
    errors: null, //menyimpan list kesalahan dari data
    errorExt: false, //parameter apakah file yang diupload sudah memiliki ekstensi yang diminta
    fileList: [], //menyimpan file yang akan diupload (temporary)
    successes: false, //parameter apakah file berhasil diupload atau tidak
    visibleModalPassword: false //parameter handle modal password
  };

  componentDidUpdate(prevProps) {
    if (this.props.importExcel.errors !== prevProps.importExcel.errors) {
      this.setState({ errors: this.props.importExcel.errors });
    } else if (
      this.props.importExcel.successes !== prevProps.importExcel.successes
    ) {
      this.setState({ successes: this.props.importExcel.successes });
    }
  }

  handleSelectAdjustmentType = id_adjustment_type => {
    if (id_adjustment_type) {
      const { adjustmentTypes } = this.props;
      const { setFieldsValue } = this.props.form;

      const adjustmentType = adjustmentTypes.find(
        data => data.id_adjustment_type === id_adjustment_type
      );

      setFieldsValue({
        id_account_adjustment: adjustmentType.id_account_adjustment,
        cost_adjustment: adjustmentType.cost_adjustment
      });
    }
  };

  handleUpload = password => {
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { fileList } = this.state;
      const { activeData, importExcelReq } = this.props;
      let body;
      fileList.forEach(file => {
        // console.log(file);
        body = {
          file: file
        };
      });
      if (activeData.key === "itemOB") {
        values.transaction_date = values.transaction_date.format(
          "YYYY-MM-DD HH:mm:ss"
        );
        body = Object.assign({}, body, password, values);
      }

      if (!err) {
        importExcelReq(activeData.key, body);
        if (activeData.key === "itemOB") {
          this.setState({
            visibleModalPassword: !this.state.visibleModalPassword
          });
        }
      }
    });
  };

  render() {
    const { errors, fileList, successes } = this.state;
    const {
      accounts,
      activeData,
      adjustmentTypes,
      loading,
      loadingForeignItemOB,
      locations
    } = this.props;
    const { getFieldDecorator } = this.props.form;

    // props untuk upload document
    const props = {
      multiple: false,
      onRemove: file => {
        this.setState(state => {
          const index = state.fileList.indexOf(file);
          const newFileList = state.fileList.slice();
          newFileList.splice(index, 1);
          return {
            fileList: newFileList
          };
        });
      },
      beforeUpload: (file, index) => {
        const name = file.name.split(".");
        const ext = name[name.length - 1]; //ambil ekstensi dari file
        if (!this.state.allowedExt.includes(ext)) {
          this.setState({
            errors: null,
            errorExt: true,
            fileList: [],
            successes: false
          });
        } else {
          this.setState({
            errors: null,
            errorExt: false,
            fileList: [file],
            successes: false
          });
        }
        return false;
      },
      fileList
    };

    // upload form
    const uploadForm = () => {
      return (
        <Upload {...props} style={{ textAlign: "right" }}>
          <Button loading={loadingForeignItemOB}>
            <Icon type="upload" /> <IntlMessages id="selectFile" />
          </Button>
        </Upload>
      );
    };

    // upload button
    const uploadButton = action => {
      return (
        <Button
          type="primary"
          onClick={action}
          loading={loadingForeignItemOB}
          disabled={fileList.length === 0}
          loading={loading}
          style={{ marginLeft: "1%" }}
        >
          {loading ? (
            <IntlMessages id="uploading" />
          ) : (
            <IntlMessages id="upload" />
          )}
        </Button>
      );
    };

    return (
      <Modal
        // {...modalRestProps}
        cancelText={getLanguage({ id: "button.cancel" })}
        centered
        closable={false}
        footer={[
          <Button
            key="back"
            disabled={loading}
            onClick={() => {
              this.setState({
                errorExt: false,
                errors: null,
                fileList: [],
                successes: false
              });
              this.props.handleCancel();
            }}
          >
            <IntlMessages id="close" />
          </Button>
        ]}
        maskClosable={false}
        title={getLanguage({ id: "upload" })}
        okText={getLanguage({ id: "button.ok" })}
        visible={this.props.visible}
        width="50%"
      >
        <WrapperModalUpload>
          {/* display judul dari modul yang akan diimport */}
          {activeData && activeData.title ? (
            <div
              style={{
                alignItems: "center",
                display: "flex",
                flexDirection: "column",
                marginBottom: "1rem"
              }}
            >
              <h3 style={{ marginBottom: ".25rem" }}>{activeData.title}</h3>
              <span style={{ fontSize: ".8rem", color: "rgba(0,0,0,0.45)" }}>
                {getLanguage({ id: "importExcel.desc" })}
              </span>
            </div>
          ) : null}

          {/* render form tambahan untuk item opening balance */}
          {activeData && activeData.key === "itemOB" ? (
            <>
              <Form layout="vertical" onSubmit={this.handleUpload}>
                <ModalPassword
                  submit={this.handleUpload}
                  visible={this.state.visibleModalPassword}
                  handleModal={() =>
                    this.setState({
                      visibleModalPassword: !this.state.visibleModalPassword
                    })
                  }
                />
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label={<IntlMessages id="issueReceipts.location" />}
                    >
                      {getFieldDecorator("id_location", {
                        // initialValue: default_employee_location.id_location,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "95%" }}
                          placeholder={
                            <IntlMessages id="issueReceipts.location" />
                          }
                          loading={loadingForeignItemOB}
                          onSelect={this.handleSelectLocation}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {locations.map((data, key) => (
                            <Option key={key} value={data.id_location}>
                              {`${data.location_code} (${data.location_name})`}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label={<IntlMessages id="issueReceipts.costAdjustment" />}
                    >
                      {getFieldDecorator("cost_adjustment", {
                        initialValue: 0
                      })(
                        <Radio.Group
                          // onChange={this.handleCostAdjustment}
                          // value={this.state.cost_adjustment}
                          buttonStyle="solid"
                          style={{ width: "100%" }}
                          disabled={true}
                        >
                          <Radio.Button
                            style={{ width: "50%", textAlign: "center" }}
                            value={0}
                          >
                            <IntlMessages id="no" />
                          </Radio.Button>
                          <Radio.Button
                            style={{ width: "50%", textAlign: "center" }}
                            value={1}
                          >
                            <IntlMessages id="yes" />
                          </Radio.Button>
                        </Radio.Group>
                      )}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label={
                        <IntlMessages id="issueReceipts.transactionDate" />
                      }
                      style={{ marginBottom: 0, paddingBottom: 0 }}
                    >
                      {getFieldDecorator("transaction_date", {
                        initialValue: moment()
                      })(
                        <DatePicker
                          style={{ width: "95%" }}
                          showTime={{ format: "HH:mm:ss" }}
                          format="DD MMMM YYYY HH:mm:ss"
                          // disabled={
                          //   !permissions.includes("Can Back Date") ? true : false
                          // }
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label={<IntlMessages id="issueReceipts.adjustmentType" />}
                    >
                      {getFieldDecorator("id_adjustment_type", {
                        // initialValue: data.id_adjustment_type,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="issueReceipts.adjustmentType" />
                          }
                          loading={loadingForeignItemOB}
                          onChange={this.handleSelectAdjustmentType}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {/* {permissions.includes("Create Adjustment Type") ? (
                          <Option
                            key={"Create Adjustment Type"}
                            value={undefined}
                            onClick={() =>
                              this.props.handleModalAddAdjustmentType()
                            }
                          >
                            <div
                              style={{
                                color: "#1890FF",
                                fontStyle: "italic"
                              }}
                            >
                              {" + " +
                                getLanguage({
                                  id: "adjustmentTypes.pageHeader"
                                })}
                            </div>
                          </Option>
                        ) : null} */}

                          {adjustmentTypes.map((data, key) => (
                            <Option key={key} value={data.id_adjustment_type}>
                              {`${data.adjustment_type_code} (${data.description})`}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label={
                        <IntlMessages id="issueReceipts.adjustmentAccount" />
                      }
                    >
                      {getFieldDecorator("id_account_adjustment", {
                        // initialValue: data.id_account_adjustment,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "95%" }}
                          placeholder={
                            <IntlMessages id="issueReceipts.adjustmentAccount" />
                          }
                          loading={loadingForeignItemOB}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {accounts.map((data, key) => (
                            <Option key={key} value={data.id_account}>
                              {`${data.account_code} ${data.account_name}`}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label={<IntlMessages id="purchaseInvoices.tabs.item" />}
                    >
                      {getFieldDecorator("excel_file", {
                        // initialValue: data.id_adjustment_type,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(uploadForm())}
                    </Form.Item>
                  </Col>
                </Row>
                <Row style={{ marginBottom: "20px" }}>
                  <Col span={12}>
                    {uploadButton(() => {
                      this.props.form.validateFieldsAndScroll((err, values) => {
                        if (!err) {
                          this.setState({ err: false });
                          return this.setState({
                            visibleModalPassword: !this.state
                              .visibleModalPassword
                          });
                        }
                      });
                    })}
                  </Col>
                </Row>
              </Form>
            </>
          ) : null}

          {/* form untuk upload file */}
          {activeData && activeData.key !== "itemOB" ? (
            <div style={{ display: "flex", justifyContent: "center" }}>
              {uploadForm()} {uploadButton(this.handleUpload)}
            </div>
          ) : null}

          {/* display result */}
          {loading ? (
            <div></div>
          ) : this.state.errorExt ? (
            <Alert
              style={{ margin: "1rem 0" }}
              message={
                <span>
                  {getLanguage({ id: "importExcel.error.ext" })}{" "}
                  <span style={{ fontWeight: "bold" }}>
                    {this.state.allowedExt.toString()}
                  </span>
                </span>
              }
              type="error"
              showIcon
            />
          ) : errors ? (
            <div style={{ margin: "1rem 0" }}>
              {/* <Alert
                style={{ margin: "1rem 0" }}
                message={getLanguage({ id: "importExcel.error.fail" })}
                type="error"
                showIcon
              /> */}
              <Alert
                style={{ maxHeight: "30vh", overflow: "auto" }}
                message={
                  <>
                    <h4>{getLanguage({ id: "importExcel.error.list" })}</h4>
                    {Object.keys(errors).map(err => (
                      <div key={err} className="list-error">
                        {getLanguage({ id: "importExcel.data.list" }) +
                          ": " +
                          (parseFloat(err.split(".")[0]) + 1)}
                        <ul>
                          {errors[err].map((data, index) => (
                            <li key={index}>{data}</li>
                          ))}
                        </ul>
                      </div>
                    ))}
                  </>
                }
                type="warning"
              />
            </div>
          ) : successes ? (
            <Alert
              style={{ margin: "1rem 0" }}
              message={getLanguage({ id: "importExcel.success" })}
              type="success"
              showIcon
            />
          ) : null}
        </WrapperModalUpload>
      </Modal>
    );
  }
}

const WrappedModalUpload = Form.create({ name: "upload_modal" })(ModalUpload);

export default WrappedModalUpload;
