import React, { Component } from "react";

import IntlMessages from "../../../components/utility/intlMessages";
import Button from "../../../components/uielements/button";
import {
  // Alert,
  Col,
  // Divider,
  Form,
  // Input,
  // Select,
  Modal,
  Row,
  Spin,
  Tabs,
  Tree
} from "antd";
import Scrollbars from "../../../components/utility/customScrollBar";
import TableWrapper from "../../Tables/antTables/antTable.style";

import getLanguage from "../../App/MyLanguange";
import {
  CashBankColumns,
  CashFlowTypeColumns,
  CurrColumns,
  CustColumns,
  CustOBColumns,
  CustTypeColumns,
  EmpColumns,
  ItemColumns,
  ItemOBColumns,
  JobTitleColumns,
  LocColumns,
  PayCatColumns,
  PayTermColumns,
  ProjectColumns,
  SupColumns,
  SupOBColumns,
  SupTypeColumns,
  TaxColumns,
  UnitColumns
} from "./columns.list";

// const { Option } = Select;
// const { TextArea } = Input;
const TabPane = Tabs.TabPane;
const { TreeNode } = Tree;

class ModalDetail extends Component {
  state = {
    detailData: null,
    autoExpandParent: true,
    expandedKeys: [],
    searchAccount: ""
  };

  componentDidUpdate(prevProps) {
    if (this.props.detailData !== prevProps.detailData) {
      this.setState({ detailData: this.props.detailData });
    }
  }

  // Handle expand tree
  onExpand = expandedKeys => {
    this.setState({
      expandedKeys,
      autoExpandParent: false
    });
  };

  // handle select tree
  handleSelectTree = e => {
    const { showDetail, clearAccount } = this.props;
    this.props.clearErrorField();

    this.setState({
      ...this.state
    });

    const value = e[0] ? e[0] : "";
    // console.log(value);
    value ? showDetail(value) : clearAccount();
  };

  // Handle pagination
  handlePageChange = page => {
    const { activeData, getList } = this.props;
    getList(activeData.key, page, null, "");
  };

  handleMappingGLAccount = conf_name => {
    const { foreignData, listData } = this.props;
    if (listData && listData.length > 0) {
      const find = listData.find(
        data => data.gl_configuration_name === conf_name
      );
      if (find) {
        const findAccount = foreignData.find(
          data => data.id_account === find.id_account
        );
        return findAccount.account_code + " (" + findAccount.account_name + ")";
      }
    }
  };

  render() {
    const { autoExpandParent, expandedKeys, searchAccount } = this.state;
    const { activeData, importExcel, listData, loading } = this.props;
    const { current_page, page_size, total } = importExcel;

    let columns = [];

    // kolom berdasarkan key dari activeData yang dipilih
    if (activeData && activeData.key) {
      switch (activeData.key) {
        case "location":
          columns = LocColumns;
          break;
        case "currency":
          columns = CurrColumns;
          break;
        case "cashFlowType":
          columns = CashFlowTypeColumns;
          break;
        case "coaAccType":
          columns = ""; //-------------------
          break;
        case "glAccount":
          columns = ""; //-------------------
          break;
        case "tax":
          columns = TaxColumns;
          break;
        case "cashBank":
          columns = CashBankColumns;
          break;
        case "paymentTerm":
          columns = PayTermColumns;
          break;
        case "paymentCategory":
          columns = PayCatColumns;
          break;
        case "supplierType":
          columns = SupTypeColumns;
          break;
        case "supplier":
          columns = SupColumns;
          break;
        case "customerType":
          columns = CustTypeColumns;
          break;
        case "customer":
          columns = CustColumns;
          break;
        case "jobTitle":
          columns = JobTitleColumns;
          break;
        case "employee":
          columns = EmpColumns;
          break;
        case "department":
          columns = "companies/departments";
          break;
        case "project":
          columns = ProjectColumns;
          break;
        case "unit":
          columns = UnitColumns;
          break;
        case "itemCategory":
          columns = "items/categories";
          break;
        case "item":
          columns = ItemColumns;
          break;
        case "itemOB":
          columns = ItemOBColumns;
          break;
        case "supplierOB":
          columns = SupOBColumns;
          break;
        case "customerOB":
          columns = CustOBColumns;
          break;
        default:
          break;
      }
    }

    // mapping untuk tampilan GL Account
    let glAccountView = [
      { conf_name: "opening_balance" },
      { conf_name: "retained_earning" },
      { conf_name: "income_summary" },
      { conf_name: "debit_memo" },
      { conf_name: "deposit_paid" },
      { conf_name: "payable" },
      { conf_name: "credit_memo" },
      { conf_name: "customer_deposit" },
      { conf_name: "direct_sales" },
      { conf_name: "delivery_order" },
      { conf_name: "receivable" },
      { conf_name: "inventory" },
      { conf_name: "sales" },
      { conf_name: "item_discount" },
      { conf_name: "sales_return" },
      { conf_name: "cogs" },
      { conf_name: "purchase_return" },
      { conf_name: "purchase_return_freight_in" },
      { conf_name: "expense" },
      { conf_name: "unbilled_goods" }
    ];
    let glAccountResult = {}; //menyimpan hasil result dari mapping berdasarkan gl_configuration_name
    if (activeData && activeData.key === "glAccount" && listData) {
      glAccountView.map(data => {
        glAccountResult[data.conf_name] = this.handleMappingGLAccount(
          data.conf_name
        );
        return glAccountResult;
      });
    }

    // loop untuk data tree
    const loop = (data, code, nameParam, id, child) =>
      data.map(item => {
        console.log("current ", item, " has item[child]", item[child]);
        const name = item[code] + " " + item[nameParam];
        const key = item[code] + " " + item[nameParam];
        const index = key.toLowerCase().indexOf(searchAccount.toLowerCase());
        const beforeStr = name.substr(0, index);
        const str = name.substr(index, searchAccount.length);
        const afterStr = name.substr(index + searchAccount.length);
        const title =
          index > -1 ? (
            <span>
              {beforeStr}
              <strong>{str}</strong>
              {afterStr}
            </span>
          ) : (
            <span>{name}</span>
          );
        if (item[child]) {
          return (
            <TreeNode
              key={item[id]}
              title={
                item.is_active ? (
                  title
                ) : (
                  <span style={{ color: "#f5222d" }}>{title}</span>
                )
              }
            >
              {loop(item[child], code, nameParam, id, child)}
            </TreeNode>
          );
        }
        return <TreeNode key={item[id]} title={title} />;
      });

    // list untuk data non table
    const RowModif = props => (
      <Row style={{ fontSize: "14px", marginBottom: "10px" }}>
        {props.left ? (
          <>
            <Col span={4} style={{ paddingLeft: "5px" }}>
              <strong>
                <IntlMessages id={props.leftLabel}></IntlMessages>
              </strong>
            </Col>
            <Col span={1}>:</Col>
            <Col span={7} style={{ paddingRight: "5px" }}>
              {props.leftData}
            </Col>
          </>
        ) : (
          <Col span={12} />
        )}
        {props.right ? (
          <>
            <Col span={4}>
              <strong>
                <IntlMessages id={props.rightLabel}></IntlMessages>
              </strong>
            </Col>
            <Col span={1}>:</Col>
            <Col span={7}>{props.rightData}</Col>
          </>
        ) : null}
      </Row>
    );

    return (
      <Modal
        // {...modalRestProps}
        cancelText={getLanguage({ id: "button.cancel" })}
        centered
        title={<IntlMessages id="List" />}
        footer={[
          <Button
            key="back"
            onClick={() => {
              this.props.handleCancel();
              this.setState({ detailData: null });
            }}
          >
            <IntlMessages id="close" />
          </Button>
        ]}
        onCancel={() => {
          this.props.handleCancel();
          this.setState({ detailData: null });
        }}
        okText={getLanguage({ id: "button.ok" })}
        visible={this.props.visible}
        width="75%"
      >
        {activeData && activeData.title ? (
          <div
            style={{
              alignItems: "center",
              display: "flex",
              flexDirection: "column",
              marginBottom: "1rem"
            }}
          >
            <h3 style={{ marginBottom: ".25rem" }}>{activeData.title}</h3>
            <span style={{ fontSize: ".8rem", color: "rgba(0,0,0,0.45)" }}>
              {getLanguage({ id: "listOf" }) + " " + activeData.title}
            </span>
          </div>
        ) : null}
        <Scrollbars style={{ width: "100%" }}>
          {loading ? (
            <Spin></Spin>
          ) : // untuk list berbentuk tree
          activeData &&
            (activeData.key === "itemCategory" ||
              activeData.key === "coaAccType" ||
              activeData.key === "department" ||
              activeData.key === "glAccount") ? (
            // tampilan untuk view GL Account
            activeData.key === "glAccount" ? (
              <Tabs defaultActiveKey="1">
                <TabPane
                  tab={<IntlMessages id="generalLedgers.tab.equity" />}
                  key="1"
                >
                  <Row>
                    <RowModif
                      left={true}
                      leftLabel="openingBalanceAccount"
                      leftData={glAccountResult.opening_balance}
                      right={true}
                      rightLabel="incomeSummaryAccount"
                      rightData={glAccountResult.income_summary}
                    />
                    <RowModif
                      left={true}
                      leftLabel="retainedEarningAccount"
                      leftData={glAccountResult.retained_earning}
                      right={false}
                    />
                  </Row>
                </TabPane>
                <TabPane tab={<IntlMessages id="Purchase" />} key="2">
                  <Row>
                    <RowModif
                      left={true}
                      leftLabel="currencies.add.payableAccount"
                      leftData={glAccountResult.payable}
                      right={true}
                      rightLabel="depositPaidAccount"
                      rightData={glAccountResult.deposit_paid}
                    />
                    <RowModif
                      left={true}
                      leftLabel="debitMemoAccount"
                      leftData={glAccountResult.debit_memo}
                      right={false}
                    />
                  </Row>
                </TabPane>
                <TabPane tab={<IntlMessages id="Sales" />} key="3">
                  <Row>
                    <RowModif
                      left={true}
                      leftLabel="currencies.add.receivableAccount"
                      leftData={glAccountResult.receivable}
                      right={true}
                      rightLabel="customerDepositAccount"
                      rightData={glAccountResult.customer_deposit}
                    />
                    <RowModif
                      left={true}
                      leftLabel="creditMemoAccount"
                      leftData={glAccountResult.credit_memo}
                      right={true}
                      rightLabel="deliveryOrderAccount"
                      rightData={glAccountResult.delivery_order}
                    />
                  </Row>
                </TabPane>
                <TabPane
                  tab={<IntlMessages id="generalLedgers.tab.defaultItem" />}
                  key="4"
                >
                  <RowModif
                    left={true}
                    leftLabel="inventoryAccount"
                    leftData={glAccountResult.inventory}
                    right={true}
                    rightLabel="cogsAccount"
                    rightData={glAccountResult.cogs}
                  />
                  <RowModif
                    left={true}
                    leftLabel="salesAccount"
                    leftData={glAccountResult.sales}
                    right={true}
                    rightLabel="purchaseReturnAccount"
                    rightData={glAccountResult.purchase_return}
                  />
                  <RowModif
                    left={true}
                    leftLabel="itemDiscountAccount"
                    leftData={glAccountResult.item_discount}
                    right={true}
                    rightLabel="purchaseReturnFreightInAccount"
                    rightData={glAccountResult.purchase_return_freight_in}
                  />
                  <RowModif
                    left={true}
                    leftLabel="salesReturnAccount"
                    leftData={glAccountResult.sales_return}
                    right={true}
                    rightLabel="expenseAccount"
                    rightData={glAccountResult.expense}
                  />
                  <RowModif
                    left={false}
                    right={true}
                    rightLabel="unbilledGoodsAccount"
                    rightData={glAccountResult.unbilled_goods}
                  />
                </TabPane>
              </Tabs>
            ) : (
              // end of tampilan untuk view GL Account
              <Row>
                <Col span={12} style={{ borderRight: "1px solid #e8e8e8" }}>
                  <Tree
                    showIcon
                    showLine
                    onExpand={this.onExpand}
                    expandedKeys={expandedKeys}
                    autoExpandParent={autoExpandParent}
                    onSelect={this.handleSelectTree}
                  >
                    {activeData.key === "coaAccType"
                      ? loop(
                          listData,
                          "account_code",
                          "account_name",
                          "id_account",
                          "children_all"
                        )
                      : activeData.key === "department"
                      ? loop(
                          listData,
                          "department_code",
                          "department_name",
                          "id_department",
                          "children"
                        )
                      : loop(
                          listData,
                          "item_category_code",
                          "item_category_name",
                          "id_item_category",
                          "children"
                        )}
                  </Tree>
                </Col>
                <Col span={12}>
                  <div style={{ padding: "1rem" }}>
                    <div style={{ fontWeight: "bold", marginBottom: ".5rem" }}>
                      {getLanguage({ id: "tooltip.detail" })}
                    </div>
                    {this.props.loadingDetail ? (
                      <Spin></Spin>
                    ) : activeData.key === "coaAccType" ? (
                      <Row>
                        {this.state.detailData ? (
                          <RowModif
                            left={true}
                            leftLabel="accounts.code"
                            leftData={this.state.detailData.account_code}
                            right={true}
                            rightLabel="accounts.name"
                            rightData={this.state.detailData.account_name}
                          ></RowModif>
                        ) : (
                          getLanguage({
                            id: "purchaseInvoices.account.placeholder"
                          })
                        )}
                      </Row>
                    ) : activeData.key === "department" ? (
                      <Row>
                        {this.state.detailData ? (
                          <RowModif
                            left={true}
                            leftLabel="code"
                            leftData={this.state.detailData.department_code}
                            right={true}
                            rightLabel="name"
                            rightData={this.state.detailData.department_name}
                          ></RowModif>
                        ) : (
                          getLanguage({
                            id: "select.department"
                          })
                        )}
                      </Row>
                    ) : (
                      <Row>
                        {this.state.detailData ? (
                          <RowModif
                            left={true}
                            leftLabel="code"
                            leftData={this.state.detailData.item_category_code}
                            right={true}
                            rightLabel="name"
                            rightData={this.state.detailData.item_category_name}
                          ></RowModif>
                        ) : (
                          getLanguage({
                            id: "selectItemCategory"
                          })
                        )}
                      </Row>
                    )}
                  </div>
                </Col>
              </Row>
            )
          ) : (
            // untuk list berbentuk tabel bisa
            <TableWrapper
              bordered={true}
              className="invoiceListTable forceHeight"
              columns={columns}
              dataSource={listData}
              loading={loading}
              pagination={
                page_size
                  ? {
                      current: current_page,
                      pageSize: parseInt(page_size),
                      total: total,
                      onChange: this.handlePageChange.bind(this)
                    }
                  : true
              }
              // rowKey={record => record}
            />
          )}
        </Scrollbars>
      </Modal>
    );
  }
}

const WrappedDetailModal = Form.create({ name: "detail_modal" })(ModalDetail);

export default WrappedDetailModal;
